<template>
	<div class="wrap">
    <TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
		<div class="orderBox">
			<TheOrderList></TheOrderList>
		</div>
	</div>
</template>

<script>
import TheOrderList from '@/components/ThePersonCenter/TheOrderList'
import TheCompiler from '@/components/ThePersonCenter/TheCompiler'
export default {

  data () {
    return {
      tabs: [
        {
          tabtxt: '已出单',
          active: false
        },
        {
          tabtxt: '保障中',
          active: true
        },
        {
          tabtxt: '全部',
          active: false
        }
      ]
    }
  },
  methods: {
    handleChangeTab (item) {
      this.tabs.find(tab => tab !== item && tab.active).active = false
      item.active = true
    }
  },
  components: {
    TheOrderList,
    TheCompiler
  }
}
</script>

<style lang="scss" scoped>
.wrap{
	box-shadow: 0px 2px 4px 0 rgba(219,219,219,.35);
	.orderBox{
		border-top: 1px solid #F1F4F4;
	}
}
</style>
